@charset "UTF-8";

@font-face {
  font-family: "ProximaNova";
  src: url("./assets/fonts/ProximaNova-Bold.woff2") format("woff"),
    url("./assets/fonts/ProximaNova-Bold.ttf") format("truetype"),
    url("./assets/fonts/ProximaNova-Bold.svg") format("svg"); /* Legacy iOS */
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

